body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(47, 174, 132);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#outbox {
  width: 84%;
  margin: 5px auto;
  margin-bottom: 10px;
}

#output {
  width: 100%;
  height: 50px;
  font-size: 45px;
  margin-top: 0px;
  margin-bottom: 0px;
}

.btn{
  width: 50px;
  height: 50px;
  margin: 5px;
}

.bt-label{
  display: flex;
    justify-content: center;
    align-items: center;
    margin: 2px;
  width: 100%;
  height: 80px;
}
.inpbt{
  width: 80px;
  height: 80px;
  font-size: 50px;
  border-radius: 5px;
  margin: 2px;
}
.inpbt:hover{
  cursor: pointer;
  background-color: rgb(228, 217, 203);
}